<template>
  <f7-page>
    <f7-navbar title="Update Price" back-link="Back" sliding></f7-navbar>
    <f7-block-title>Drinks</f7-block-title>
    <f7-list form v-if="prices">
      <f7-list-item>
        <f7-label>Orange</f7-label>
        <f7-input type="number" :value="prices.orange" @change="onChange($event, 'orange')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Apple</f7-label>
        <f7-input type="number" :value="prices.apple" @change="onChange($event, 'apple')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Lemonade</f7-label>
        <f7-input type="number" :value="prices.lemonade" @change="onChange($event, 'lemonade')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Cola</f7-label>
        <f7-input type="number" :value="prices.cola" @change="onChange($event, 'cola')"/>
      </f7-list-item>
    </f7-list>
  </f7-page>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      prices: null
    }
  },
  created () {
    firebase.database().ref('drinks').once('value').then(snapshot => {
      this.prices = Object.assign({}, snapshot.val() || {
          orange: 1,
          apple: 1,
          lemonade: 1.5,
          cola: 1.5
        })

    })
  },
  methods: {
    onChange (e, which) {
      const newVal = parseFloat(e.target.value)
      this.prices = Object.assign({}, this.prices, { [which]: newVal})
      firebase.database().ref('drinks/' + which).set(newVal)
    }
  }
}
</script>


