<template>
  <f7-page>
    <f7-navbar title="Update Price" back-link="Back" sliding></f7-navbar>
    <f7-block-title>Menu 1</f7-block-title>
    <f7-list form v-if="prices">
      <f7-list-item>
        <f7-label>Sandwich</f7-label>
        <f7-input type="number" :value="prices.sandwich" @change="onChange($event, 'sandwich')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Hotdog</f7-label>
        <f7-input type="number" :value="prices.hotdog" @change="onChange($event, 'hotdog')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Burger</f7-label>
        <f7-input type="number" :value="prices.burger" @change="onChange($event, 'burger')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Big Burger</f7-label>
        <f7-input type="number" :value="prices.bigBurger" @change="onChange($event, 'bigBurger')"/>
      </f7-list-item>
    </f7-list>
  </f7-page>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      prices: null
    }
  },
  created () {
    firebase.database().ref('menu1').once('value').then(snapshot => {
      this.prices = Object.assign({}, snapshot.val() || {
        sandwich: 3,
        hotdog: 4,
        burger: 3.5,
        bigBurger: 4.5
      })

    })
  },
  methods: {
    onChange (e, which) {
      const newVal = parseFloat(e.target.value)
      this.prices = Object.assign({}, this.prices, { [which]: newVal})
      firebase.database().ref('menu1/' + which).set(newVal)
    }
  }
}
</script>


