<template>
  <f7-page>
    <f7-navbar title="Stock" back-link="Back" sliding></f7-navbar>
    <f7-block-title>Stock</f7-block-title>
    <f7-list form v-if="ofs">
      <f7-list-item>
        <f7-label>Pizza</f7-label>
        <f7-input type="switch" @change="onChange('pizza')" :checked="ofs.pizza"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Sandwich</f7-label>
        <f7-input type="switch" @change="onChange('sandwich')" :checked="ofs.sandwich"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Salad</f7-label>
        <f7-input type="switch" @change="onChange('salad')" :checked="ofs.salad"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Burger</f7-label>
        <f7-input type="switch" @change="onChange('burger')" :checked="ofs.burger"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Hotdog</f7-label>
        <f7-input type="switch" @change="onChange('hotdog')" :checked="ofs.hotdog"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Steak</f7-label>
        <f7-input type="switch" @change="onChange('steak')" :checked="ofs.steak"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Big Burger</f7-label>
        <f7-input type="switch" @change="onChange('bigBurger')" :checked="ofs.bigBurger"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Drink</f7-label>
        <f7-input type="switch" @change="onChange('drink')" :checked="ofs.drink"></f7-input>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Fries</f7-label>
        <f7-input type="switch" @change="onChange('fries')" :checked="ofs.fries"></f7-input>
      </f7-list-item>
    </f7-list>
  </f7-page>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      ofs: null
    }
  },
  created () {
    firebase.database().ref('stock').once('value').then(snapshot => {
      this.ofs = Object.assign({}, snapshot.val() || {
        pizza: false,
        burger: false,
        bidBurger: false,
        sandwich: false,
        hotdog: false,
        drink: false,
        salad: false,
        steak: false,
        fries: false
      })

    })
  },
  methods: {
    onChange (which) {
      const newVal = !this.ofs[which]
      this.ofs = Object.assign({}, this.ofs, { [which]: newVal})
      firebase.database().ref('stock/' + which).set(newVal)
    }
  }
}
</script>


