<template>
  <f7-page>
    <f7-navbar title="Update Price" back-link="Back" sliding></f7-navbar>
    <f7-block-title>Menu 2</f7-block-title>
    <f7-list form v-if="prices">
      <f7-list-item>
        <f7-label>Fries</f7-label>
        <f7-input type="number" :value="prices.fries" @change="onChange($event, 'fries')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Steak</f7-label>
        <f7-input type="number" :value="prices.steak" @change="onChange($event, 'steak')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Pizza</f7-label>
        <f7-input type="number" :value="prices.pizza" @change="onChange($event, 'pizza')"/>
      </f7-list-item>
      <f7-list-item>
        <f7-label>Salad</f7-label>
        <f7-input type="number" :value="prices.salad" @change="onChange($event, 'salad')"/>
      </f7-list-item>
    </f7-list>
  </f7-page>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      prices: null
    }
  },
  created () {
    firebase.database().ref('menu2').once('value').then(snapshot => {
      this.prices = Object.assign({}, snapshot.val() || {
        fries: 1.2,
        steak: 6.5,
        pizza: 5,
        salad: 1
      })

    })
  },
  methods: {
    onChange (e, which) {
      const newVal = parseFloat(e.target.value)
      this.prices = Object.assign({}, this.prices, { [which]: newVal})
      firebase.database().ref('menu2/' + which).set(newVal)
    }
  }
}
</script>


