<template>
  <!-- App -->
  <div id="app">

    <!-- Statusbar -->
    <f7-statusbar></f7-statusbar>

    <!-- Main Views -->
    <f7-views>
      <f7-view id="main-view" navbar-through :dynamic-navbar="true" main>
        <!-- iOS Theme Navbar -->
        <f7-navbar v-if="$theme.ios">
          <f7-nav-center sliding>Jooku</f7-nav-center>
        </f7-navbar>
        <!-- Pages -->
        <f7-pages>
          <f7-page>
            <!-- Material Theme Navbar -->
            <f7-navbar v-if="$theme.material">
              <f7-nav-left>
                <f7-link icon="icon-bars" open-panel="left"></f7-link>
              </f7-nav-left>
              <f7-nav-center sliding>Jooku</f7-nav-center>
              <f7-nav-right>
                <f7-link icon="icon-bars" open-panel="right"></f7-link>
              </f7-nav-right>
            </f7-navbar>
            <!-- Page Content -->
            <f7-block-title>Promotion Screen</f7-block-title>
            <f7-list>
              <f7-list-item smart-select title="Promotion" v-if="promotion">
                <!-- Select with values inside -->
                <select name="promotion" @change="updatedPromotion($event)">
                  <option value="pizza" :selected="promotion === 'pizza'">Pizza</option>
                  <option value="bigBurger":selected="promotion === 'bigBurger'">Big Burger</option>
                  <option value="hotdog":selected="promotion === 'hotdog'">Hotdog</option>
                  <option value="foodBar":selected="promotion === 'foodBar'">Food Bar</option>
                </select>
              </f7-list-item>
              <f7-list-item link="/scan" title="Scan"></f7-list-item>
            </f7-list>
            <f7-block-title>Stock</f7-block-title>
            <f7-list>
              <f7-list-item link="/stock" title="Mark items as in/out of stock here."></f7-list-item>
            </f7-list>
            <f7-block-title>Update Price</f7-block-title>
            <f7-list>
              <f7-list-item link="/menu1" title="Menu 1"></f7-list-item>
              <f7-list-item link="/menu2" title="Menu 2"></f7-list-item>
              <f7-list-item link="/drinks" title="Drinks"></f7-list-item>
            </f7-list>
          </f7-page>
        </f7-pages>
      </f7-view>
    </f7-views>
  </div>
</template>

<script>
import firebase from 'firebase'
export default {
  data () {
    return {
      promotion: null
    }
  },
  created () {
    firebase.database().ref('promotion').once('value').then(snapshot => {
      this.promotion = snapshot.val() || ''
    })
  },
  methods: {
    updatedPromotion (e) {
      const newVal = e.target.value
      this.promotion = newVal
      firebase.database().ref('promotion/').set(newVal)
    }
  }
}
</script>
<style>
.list-block .item-input {
  width: unset !important;
}
</style>


