<template>
  <f7-page>
    <f7-navbar title="Scan" back-link="Back" sliding></f7-navbar>
    <f7-block inner>Scan QRCode to update promotion screen.</f7-block>
    <div class="wrapper">
      <qrcode-reader class="qrcode-reader" @decode="onDecode"></qrcode-reader>
      <p v-show="url" id="result">Result:</p>
      <iframe :src="url" v-show="url"></iframe>
    </div>
  </f7-page>
</template>

<script>
export default {
  data () {
    return {
      prices: null,
      msg: '',
      url: ''
    }
  },
  methods: {
    onDecode (content) {
      this.msg = `Tried to open: \n${content}.\n If not opened, please click on the link.`
      this.url = content
      window.location.href = 'https://kang-cheng.github.io/jooku/setting/#result'
    }
  }
}
</script>
<style scoped>
.list-block {
  display: flex;
  justify-content: center;
  align-items: center;
}.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
iframe {
  width: 80%;
  height: 60px;
}
</style>


